.fullscreen {
  position: fixed;
  z-index: 2;
  width: 100%;
  height: 100%; }

.fullscreen > .content {
  position: relative;
  z-index: 3;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 2rem;
  padding-bottom: 2rem; }

.fullscreen .bg {
  z-index: 2;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0.66;
  background: #fff;
  transition: opacity 0.6s cubic-bezier(0.25, 0.1, 0.25, 1);
  animation-name: slowOpacityDrop;
  animation-iteration-count: 1;
  animation-timing-function: ease-out;
  animation-duration: 10s; }

body.mobile .fullscreen .bg {
  opacity: 0.33 !important;
  background: url("../images/satori-short-first-frame.jpg") no-repeat center center fixed;
  background-size: cover;
  animation-name: slowOpacityIncrease;
  animation-iteration-count: 1;
  animation-timing-function: ease-out;
  animation-duration: 10s; }

.fullscreen.more-opaque .bg {
  opacity: 0.9; }

@-moz-keyframes slowOpacityDrop {
  0% {
    opacity: 1; }
  100% {
    opacity: 0.66; } }
@-webkit-keyframes slowOpacityDrop {
  0% {
    opacity: 1; }
  100% {
    opacity: 0.66; } }
@keyframes slowOpacityDrop {
  0% {
    opacity: 1; }
  100% {
    opacity: 0.66; } }
@-moz-keyframes slowOpacityIncrease {
  0% {
    opacity: 0; }
  100% {
    opacity: 0.33; } }
@-webkit-keyframes slowOpacityIncrease {
  0% {
    opacity: 0; }
  100% {
    opacity: 0.33; } }
@keyframes slowOpacityIncrease {
  0% {
    opacity: 0; }
  100% {
    opacity: 0.33; } }
.fullscreen video.bg-video {
  z-index: 1; }

video.bg-video {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  -ms-transform: translateX(-50%) translateY(-50%);
  -moz-transform: translateX(-50%) translateY(-50%);
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
  background: url("../images/satori-short-first-frame.jpg") no-repeat;
  background-size: cover; }

body.mobile video.bg-video {
  display: none; }

.fullscreen-blocker {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100%; }

.pointer-wrapper {
  position: absolute;
  z-index: 2;
  top: -3.25rem;
  left: 50%;
  overflow: visible;
  transform: translateX(-50%) scale(1);
  animation-name: delayedScaleUp;
  animation-iteration-count: 1;
  animation-timing-function: ease-out;
  animation-duration: 5s; }

@-moz-keyframes delayedScaleUp {
  0% {
    -moz-transform: translateX(-50%) scale(0); }
  95% {
    -moz-transform: translateX(-50%) scale(0); }
  100% {
    -moz-transform: translateX(-50%) scale(1); } }
@-webkit-keyframes delayedScaleUp {
  0% {
    -webkit-transform: translateX(-50%) scale(0); }
  95% {
    -webkit-transform: translateX(-50%) scale(0); }
  100% {
    -webkit-transform: translateX(-50%) scale(1); } }
@keyframes delayedScaleUp {
  0% {
    transform: translateX(-50%) scale(0); }
  95% {
    transform: translateX(-50%) scale(0); }
  100% {
    transform: translateX(-50%) scale(1); } }
.pointer {
  bottom: 0.75rem;
  padding: 0.5rem 1rem;
  border-radius: 0.25rem;
  transform: translateY(0);
  color: #fff;
  white-space: nowrap;
  background: rgba(0, 0, 0, 0.9);
  animation-name: slowDownTopMove;
  animation-iteration-count: infinite;
  animation-timing-function: ease-out;
  animation-duration: 2s; }
  .pointer:after {
    position: absolute;
    top: 100%;
    left: 50%;
    width: 0;
    height: 0;
    content: '';
    border: solid transparent;
    pointer-events: none;
    border-color: transparent;
    border-top-color: rgba(0, 0, 0, 0.9);
    border-width: 0.5rem;
    margin-left: -0.5rem; }

@-moz-keyframes slowDownTopMove {
  0% {
    -moz-transform: translateY(0); }
  25% {
    -moz-transform: translateY(0.5rem); }
  100% {
    -moz-transform: translateY(0); } }
@-webkit-keyframes slowDownTopMove {
  0% {
    -webkit-transform: translateY(0); }
  25% {
    -webkit-transform: translateY(0.5rem); }
  100% {
    -webkit-transform: translateY(0); } }
@keyframes slowDownTopMove {
  0% {
    transform: translateY(0); }
  25% {
    transform: translateY(0.5rem); }
  100% {
    transform: translateY(0); } }
